import React from "react";
import { makeStyles } from '@material-ui/core/styles';
import MenuIcon from "@material-ui/icons/Menu";
import {Toolbar, IconButton} from "@material-ui/core";
import logo from '../images/logo.png'
import * as Constant from "../Constants"
import clsx from "clsx";

const useStyles = makeStyles((theme) => ({
    root:{
        overflowX:"hidden",
        background: "rgba(0,0,0,0)",
        zIndex:0
    },
    toobar:{
        position:"fixed",
        width:"100%",
        paddingLeft:0,
        height:"5vh",
        background:"#000",
        zIndex:1000,
        opacity:0
    },
    toobarOpen:{
        animation: `$openAppbar 1s`,
        opacity:100,
    },
    toobarClose:{
        opacity:100,
        transform: "translateX(100vw)",
        animation: `$closeAppbar 1s`,
    },
    "@keyframes openAppbar": {
        "0%"   :{ transform: "translateX(100vw) " },
        "100%" :{ transform: "translateX(0) " }
    },
    "@keyframes closeAppbar": {
        "0%"   :{ transform: "translateX(0);" },
        "100%" :{ transform: "translateX(100vw) " }
    },
    iconButton: {
        color:"#FFF",
        margin:"2px"
    },
    menuButton:{
        color:"#FFF",
        zIndex:1001,
        position:"fixed",
        top:"10px",
        right:"20px",
        height:"5vh",
    },
    logo:{
        width:"5vh",
        height:"5vh",
        position:"absolute",
        left:"20px",
    },
    buttonContainer:{
        marginLeft:"auto",
        marginRight:"auto",
    },
}))

function CustomAppbar(props){
    const classes = useStyles(props)
    const buttonList = Constant.BUTTON_LIST
    const closeAppbar = !props.openAppbar && props.openAppbar !== null 

    return (
        <>
          <div className={classes.root} position="static">
            <Toolbar className={clsx(classes.toobar, {[classes.toobarOpen]: props.openAppbar}, { [classes.toobarClose]: closeAppbar})}>
                <img src={logo} className={classes.logo}/>
                <div className={classes.buttonContainer}>
                    {Object.keys(buttonList).map((text, index) => (
                        <IconButton
                            key={index}
                            edge="start"
                            className={classes.iconButton}
                            color="inherit"
                            aria-label="open drawer"
                            onClick={()=>props.history.push("/" + text)}
                        >
                            {buttonList[text]}
                        </IconButton>
                    ))}
                </div>
            </Toolbar>
          </div>
          {props.children}
        </>
      );
}

function Navbar(props){
    const {menuButton} = useStyles(props)
    const setOpenAppbar = props.setOpenAppbar
    const openAppbar = props.openAppbar
    return(
        <div >
            <MenuIcon onClick={()=>setOpenAppbar(!openAppbar)} className={menuButton}/>
            <CustomAppbar {...props} openAppbar={openAppbar}>
                {props.children}
            </CustomAppbar>
        </div>
    )
    
}


export default Navbar