import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import * as Constant from "../Constants"
import profile from "../images/profile.jpg"
import AnimatedText from '../Components/AnimatedText';
import { Button } from '@material-ui/core';
import AnimatedCircle from '../Components/AnimatedCircle';

const useStyles = makeStyles((theme) => ({
    root: {    },
    background:{
        float: "left",
        display: "block",
        position: "absolute",
        top:0,
        width: "100vw",
        height: "35vh",
        zIndex:-10
    },
    profile:{
        display:"block",
        margin: "2%",
        width: "96%",
        height: "96%",
        borderRadius:"50%",
    },
    profileBorder:{
        top: "20vh",
        background: "#FFFFFF",
        height: "30vh",
        width: "30vh",
        position: "absolute",
        marginLeft:"auto",
        marginRight:"auto",
        left: 0,
        right: 0,
        display: "block",
        borderRadius:"50%",
        zIndex: 50,
    },
    textContainer:{
        marginTop:"55vh",
        display:"block",

    },
    title:{
        color: Constant.TEXT_COLOR_2,
        // fontSize:'500%',
        fontWeight:"bolder",
        zIndex: 50,
        textAlign:"center",
    },
    subtitle:{
        color: Constant.TEXT_COLOR_2,
        textAlign:"center",
        fontWeight:"bold",
    },
    button: {
        position: "absolute",
        left:0,
        right:0,
        marginLeft:"auto",
        marginRight:"auto",
        bottom:"15%"
    },
}))


const Background = React.memo(function Background({  }) {
    const classes = useStyles()
    return (
        <div className={classes.background}>
            <AnimatedCircle bottom="-65%" style={{height:"100%"}}/>
        </div>
    );
});



function Home(props) {
    const classes = useStyles(props)

    return (
        <main className={classes.root}>
            <Background/>
            <div className={classes.textContainer}>
                <h1 className={classes.title}>
                    PNG QUN JIA
                </h1>
                <h3 className={classes.subtitle}>
                    WEB DEVELOPER | MACHINE LEARNING ENTHUSIAST
                </h3>
            </div>
            <div className={classes.profileBorder}>
                <img className={classes.profile} src={profile} />
            </div>

            <Button onClick={()=> props.history.push("/contact")} className={classes.button} variant="contained" color="primary" >Contact Me!</Button>
        </main>
    );
}

export default Home;