import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Paper} from '@material-ui/core';
import * as Constant from '../../Constants'
import AnimatedText from '../../Components/AnimatedText';
import FaceLmkCustomDialog from './FaceLmkCustomDialog';
import face_lmk_video from '../../images/face_lmk_2.mp4'
import scrolldowngif from '../../images/scroll-down.gif'
import face_lmk_img_2 from '../../images/face_lmk_2.png'
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    root: {
        // height: "100vh",
        // overflow:"hidden",
    },
    video :{
        width:"100vw",
        height: "56vw",
        zIndex:2,
        position:"absolute",
        marginTop:0
    },
    img :{
        maxWidth:"80vw",
        maxHeight:"80vh",
        display:"block",
        marginLeft:"auto",
        marginRight:"auto",
        position:"relative",
        zIndex:30
    },
    button:{
        position:"relative",
        zIndex:30,
        marginTop: "15vh",
        width:"25vw",
        marginLeft:"37.5vw",
    },
    scrollDownGIF:{
        position:"absolute", 
        display:"block",
        width:"10vw",
        marginLeft:"45vw",
        marginTop:"35vw",
        zIndex:30
    },
    subtitle: {
        fontSize: "300%",
        marginLeft:"15vw",
        color: Constant.TEXT_COLOR_1,
        fontWeight:"bolder"
    },
    title: {
        // textAlign: "center",
        color: Constant.TEXT_COLOR_1,
        width:"70vw",
        marginLeft:"15vw",
        fontSize: "400%",
        margin: 0,
        display:"block",
        position:"relative",
        zIndex:10
    },
    link:{
        fontWeight:"bolder",
        color:"#F8F8FF",
        cursor:"pointer"
    },
    paragraph: Constant.PROJECT_PARAGRAPH
}))

const theme = createMuiTheme({ 
    overrides: {
        MuiButton: {
          root: {
            borderRadius: 800,
          }, 
        }, 
      }, 
});

const Face_lmk = React.memo(function Face_lmk(props) {
    const classes = useStyles(props);      
    const [dialogOpen, setDialogOpen] = React.useState(false)

    return (
        <>
            <img className={classes.scrollDownGIF} src={scrolldowngif} alt="scroll down for more infomation about this project!" onClick={()=>window.scrollTo({top: window.innerHeight, behavior: 'smooth'})}/>

            <Paper className={classes.video} style={{background:"#000000"}}>
                <video className={classes.video} style={{opacity:0.3}} loop playsInline autoPlay="autoplay" muted >
                    <source src={face_lmk_video} type="video/mp4"/>
                </video>
            </Paper>

            <h1 className={classes.title} style={{marginTop:"10vh"}}>
                <AnimatedText text="Single-Stage Multi Person" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>

            <h1 className={classes.title}>
                <AnimatedText text="Face Detector with Landmarks" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>

            <FaceLmkCustomDialog open={dialogOpen} setDialogOpen={setDialogOpen}/>
            <MuiThemeProvider theme={theme}>
                <Button className={classes.button} variant="contained" color="secondary" onClick={()=>setDialogOpen(true)}>Try out my Face Detector API!</Button>
            </MuiThemeProvider>
            <h2 className={classes.subtitle} style={{marginTop:"35vw"}}>
                <AnimatedText text="Introduction" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h2>

            <div align="justify">
                <p className={classes.paragraph}>
                    Can a neural network learn to identify people's face in digital images? 
                    What about localising the position of facial landmarks like eyes, nose, and mouth? 
                    These tasks may be intuitive for humans, but computers need a complex algorithm 
                    to perform this task. In this project, I have created a neural network that can
                    perform these face localisation tasks together in real-time.
                </p>
            </div>

            <h2 className={classes.subtitle}>
                <AnimatedText text="Methodology" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h2>

            <div align="justify">
                <p className={classes.paragraph}>
                    Current multi-person face landmark detection methods can be classified into two categories: 
                    "two-stage" methods and "one-stage" methods. "Two-stage" methods employ an object detection 
                    network for face detection (stage 1), crops the face from the image, and localise the 
                    landmarks using a landmark detection network based on regression (stage 2). By contrast, 
                    single-stage methods employ one standalone network for face detection and facial landmark 
                    detection tasks, which is more efficient but less accurate.
                </p>
            </div>

            <img className={classes.img} src={face_lmk_img_2} alt='Comparison of "two-stage" methods and "one-stage" methods'></img>

            <div align="justify">
                <p className={classes.paragraph}>
                    You Only Look Once (YOLOv4)
                    was used as the base algorithm for this project. YOLOv4 is a highly efficient algorithm
                    for object detection which can run at 65 FPS on Tesla V100 GPU. However, YOLOv4 is 
                    unable to perform landmark detection. Therefore, to incorporate this feature, the output
                    layer of YOLOv4 was modified to become a "one-stage" network. Additionally, several 
                    experiments and data augmentations were also conducted to improve the accuracy of the network. 
                </p>
            </div>

            <div align="justify">
                <p className={classes.paragraph}>
                    Click <a className={classes.link} onClick={()=>setDialogOpen(true)}><u>here</u></a> to test out my Face Detector API.
                </p>
            </div>
        </>
    );
})

export default Face_lmk;