import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import * as Constant from '../../Constants'
import AnimatedText from '../../Components/AnimatedText';
import CustomCarousel from '../../Components/CustomCarousel';
import wati_header_1 from '../../images/wati_header_1.png'
import wati_header_2 from '../../images/wati_header_2.png'
import wati_header_3 from '../../images/wati_header_3.png'

const useStyles = makeStyles((theme) => ({
    root: {
        // height: "100vh",
        // overflow:"hidden",
    },
    img :{
        maxWidth:"70vw",
        maxHeight:"80vh",
        display:"block",
        marginLeft:"auto",
        marginRight:"auto",
        position:"relative",
        zIndex:30
    },
    button:{
        position:"relative",
        zIndex:30,
        marginTop: "15vh",
        width:"12vw",
        marginLeft:"44vw",
    },
    link:{
        fontWeight:"bolder",
        color:"#F8F8FF"
    },
    subtitle: {
        fontSize: "300%",
        marginLeft:"15vw",
        color: Constant.TEXT_COLOR_1,
        fontWeight:"bolder"
    },
    title: {
        textAlign: "center",
        color: Constant.TEXT_COLOR_1,
        width:"70vw",
        marginLeft:"15vw",
        fontSize: "400%",
        margin: 0,
        display:"block",
        position:"relative",
        zIndex:10
    },
    carousel:{
        width:"70vw",
        marginLeft:"15vw",
        zIndex:100
    }, 
    carouselImg:{
        width:"70vw",
        opacity:0.8,
        marginLeft:0
    }, 
    paragraph: Constant.PROJECT_PARAGRAPH
}))

const Space = React.memo(function Space(props) {
    const classes = useStyles(props);      

    return (
        <>
            <h1 className={classes.title} style={{marginTop:"10vh"}}>
                <AnimatedText text="Wisconsin Assistive" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>

            <h1 className={classes.title}>
                <AnimatedText text="Technology Initiative" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>

            <h2 className={classes.subtitle} >
                <AnimatedText text="Introduction" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h2>

            <div align="justify">
                <p className={classes.paragraph}>
                    The Wisconsin Assistive Technology Initiative (WATI) Assessment Package is a 
                    systematic, stepwise framework designed to help in the assistive technology (AT) 
                    decision-making process for students with disabilities in their customary environments.
                </p>
            </div>

            <div align="justify">
                <p className={classes.paragraph}>
                    National Institute of Education (NIE) Singapore use WATI framework as a guide for teachers 
                    to determine, administer, train, implement and monitor assistive technology for students with 
                    special needs.
                </p>
            </div>

            <div align="justify">
                <p className={classes.paragraph}>
                    In this project, I have created a web application for teachers to access WATI assessment easily. 
                </p>
            </div>


            <CustomCarousel height="39.375vw" items={[wati_header_1,wati_header_2,wati_header_3]}/>
        </>
    );
})

export default Space;