
import React from 'react';
import {  makeStyles } from '@material-ui/core/styles';

let translateYHeight = "-150vh"

const useStyles = makeStyles((theme) => ({
    animatedCircleContainer: {
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        margin:0,
        padding:0,
        zIndex: 1,
        overflow:"hidden",
        background: "linear-gradient(170deg, #000000 0%, #04619f 74%)",
    },
    animatedCircleLi: {
        position: "absolute",
		listStyle: "none",
		display: "block",
		width: props=> props.size,
		height: props=> props.size,
        backgroundColor: "#FFFFFF",
        opacity: props=> props.opacity,
		bottom: props=> props.bottom,
        animation: `$circle 25s infinite`,
        transitionTimingFunction: "linear",
        left: props=> props.left,
        animationDelay: props=> props.animationDelay,
        animationDuration: props=> props.animationDuration,
        borderRadius: "50%"
    },
    animatedError404: {
        position: "absolute",
		display: "block",
		width: props=> props.size,
		height: props=> props.size,
        color: "#FFFFFF",
        opacity: props=> props.opacity,
		bottom: props=> props.bottom,
        animation: `$circle 25s infinite`,
        transitionTimingFunction: "linear",
        left: props=> props.left,
        animationDelay: props=> props.animationDelay,
        animationDuration: props=> props.animationDuration,
    },
    "@keyframes circle": {
        "0%"   :{ transform: "translateY(0);" },
        "100%" :{ transform: "translateY(" + translateYHeight + ") rotate(600deg)" }
    }
}))

const Circle = React.memo(function Circle(props) {
    const {animatedCircleLi} = useStyles(props)
    return (<li className={animatedCircleLi}></li>)
})

const Error404 = React.memo(function Error404(props) {
    const {animatedError404} = useStyles(props)
    return (<span className={animatedError404}>44</span>)
})


const AnimatedCircle = React.memo(function AnimatedCircle(props) {
    let scale = 1;
    if (props.translateYHeight !== undefined){
        translateYHeight=props.translateYHeight
        scale = parseInt(translateYHeight.replace("vh", "")) / -150
    }

    const size =              ["40px", "80px", "60px", "40px", "120px", "160px", "20px", "10px",  "160px",]
    const left =              ["10%", "20%", "25%", "70%", "80%", "32%", "55%", "25%", "90%"]
    const opacity =           ["10%", "10%", "25%", "10%", "20%","15%", "10%", "30%", "10%"]
    const animationDelay =    ["0s", "2s", "0s", "0s", "3s", "7s", "15s", "2s", "11s"]
    const animationDuration = [12, 17, 22, 25, 25, 25, 40, 40, 25]

    let li = []
    const {animatedCircleContainer} = useStyles(props)

    for (let i = 0; i < 10; i++){
        let circleProps = {
            size : size[i],
            left : left[i],
            opacity : opacity[i],
            animationDelay : animationDelay[i],
            animationDuration : (animationDuration[i] * scale).toString() + "s",
            bottom: props.bottom
        }

        if (props.error404){
            li.push(<Error404 key={i} {...circleProps} ></Error404>)
        } else{
            li.push(<Circle key={i} {...circleProps} ></Circle>)
        }

    }

    return (
        <div>
            <ul className={animatedCircleContainer} style={props.style}>
            {props.children}
                {li}
            </ul>
        </div>
    )
})

export default AnimatedCircle;