import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import * as Constant from "../Constants"
import profile from "../images/profile.jpg"
import AnimatedText from '../Components/AnimatedText';
import { Button } from '@material-ui/core';
import AnimatedCircle from '../Components/AnimatedCircle';

const useStyles = makeStyles((theme) => ({
    root: {    },
    background:{
        float: "left",
        display: "block",
        position: "absolute",
        width: "100%",
        height: "60vh",
    },
    profile:{
        display:"block",
        margin: "2%",
        width: "96%",
        height: "96%",
        borderRadius:"50%",
    },
    profileBorder:{
        background: "#FFFFFF",
        height: "70%",
        width: "70vh",
        position: "absolute",
        left: "60%",
        top: "20%",
        display: "block",
        borderRadius:"50%",
        zIndex: 50,
    },
    title:{
        color: Constant.TEXT_COLOR_1,
        position: "absolute",
        left:"10%",
        bottom:"0%",
        margin:0,
        fontSize:'500%',
        fontWeight:"bolder",
        zIndex: 50,
    },
    subtitle:{
        color: Constant.TEXT_COLOR_2,
        position: "absolute",
        fontSize:'150%',
        fontWeight:"bold",
        margin:0,
        left:"10%",
        top:"100%",
    },
    button: {
        position: "absolute",
        left:"10%",
        bottom:"20%"
    },
}))


const Background = React.memo(function Background({  }) {
    const classes = useStyles()
    return (
        <div className={classes.background}>
            <h1 className={classes.title}>
                <AnimatedText text="PNG QUN JIA" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>
            <h3 className={classes.subtitle}>
                <AnimatedText text="WEB DEVELOPER | MACHINE LEARNING ENTHUSIAST" alt_color={Constant.TEXT_COLOR_1} ></AnimatedText>
            </h3>
            <AnimatedCircle bottom="-45%" style={{height:"100%"}}/>
        </div>
    );
});



function Home(props) {
    const classes = useStyles(props)

    return (
        <main className={classes.root}>
            <Background/>
            <div className={classes.profileBorder}>
                <img className={classes.profile} src={profile} />
            </div>]

            <Button onClick={()=> props.history.push("/contact")} className={classes.button} variant="contained" color="primary" >Contact Me!</Button>
        </main>
    );
}

export default Home;