import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";

function GetUseStyles(alt_color) {
    const useStyles = makeStyles(theme => ({
        animatedLetter: {
            display:"inline-block",
            animation: `$myEffect 0.5s ${theme.transitions.easing.easeInOut}`,
        },
        animatedLetterExiting: {
            animation: `$myEffectExit 0.5s ${theme.transitions.easing.easeInOut}`,
        },
        "@keyframes myEffect": {
            "0%": { transform: "translate(1px, 1px) rotate(0deg)" , color: alt_color},
            "20%": { transform: "translate(-3px, 0px) rotate(1deg) scale(1.4,1.4)" },
            "40%": { transform: "translate(1px, -1px) rotate(1deg)" },
            "60%": { transform: "translate(-3px, 1px) rotate(0deg)" },
            "80%": { transform: "translate(-1px, -1px) rotate(1deg)" },
            "100%": { transform: "translate(1px, -2px) rotate(-1deg)" },
        },
        "@keyframes myEffectExit": {
            "0%": { transform: "translate(-1px, -1px) rotate(0deg)", color: alt_color },
            "20%": { transform: "translate(3px, 0px) rotate(1deg)" },
            "40%": { transform: "translate(-1px, 1px) rotate(1deg)" },
            "60%": { transform: "translate(3px, -1px) rotate(0deg)" },
            "80%": { transform: "translate(1px, 1px) rotate(1deg)" },
            "100%": { transform: "translate(-1px, 2px) rotate(-1deg)" },
        }
    }));

    return useStyles()
}

function AnimatedTextLetter(props) {
    const classes = GetUseStyles(props.alt_color);
    const [exit, setExit] = React.useState(true);
    return (
      <span
          className={clsx(classes.animatedLetter, {
          [classes.animatedLetterExiting]: exit
          })}
          onMouseEnter={()=> setExit(true)}
          onMouseOut={()=> setExit(false)}
        >
          {props.letter}
      </span>
    );
  }

const AnimatedText = React.memo(function AnimatedText(props) {
  return (
    <>
      {props.text.split("").map((letter, index)=> 
              letter === " " ? 
              <span key={index}> </span>
              :
              <AnimatedTextLetter key={index} {...props} letter={letter}/>
      )}
    </>
  );
})
  
  export default AnimatedText