import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import * as Constant from  '../../Constants'

const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
    },
    icon: props => ({
        color: props.isSelected || props.isHover ? Constant.TEXT_COLOR_3 : Constant.TEXT_COLOR_1,
        display: "block",
        textAlign: "center",
        height:"4vh",
    }),
    text:{
        color: Constant.TEXT_COLOR_3,
        display: "block",
        textAlign: "center",
        fontSize:'0.6em',
        fontWeight:"bolder",
        height:"4vh",
    }
}));

function CustomListItem(props) {
  const classes = useStyles(props);

  return (
      <>
      {
        props.isHover? 
        <ListItemText classes={{primary:classes.text}} primary={props.text.toUpperCase()} />
        :
        <ListItemIcon className={classes.icon}>{props.icon}</ListItemIcon>
      }
      </>
  )
}



export default CustomListItem;

