import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import * as Constant from "../Constants"
import AnimatedText from '../Components/AnimatedText';
import AnimatedCircle from '../Components/AnimatedCircle';
import { Button } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    root:{
        height: "100%",
    },
    title:{
        color: Constant.TEXT_COLOR_1,
        position: "absolute",
        left:"0%",
        right:"0%",
        textAlign:"center",
        top:"20%",
        fontSize:'500%',
        fontWeight:"bolder",
        zIndex:30
    },
    subtitle:{
        color: Constant.TEXT_COLOR_1,
        position: "absolute",
        left:"0%",
        right:"0%",
        textAlign:"center",
        top:"40%",
        fontSize:'200%',
        fontWeight:"bolder",
        zIndex:30

    },
    button:{
        width:"70vw",
        position: "absolute",
        left:"0%",
        right:"0%",
        marginLeft:"auto",
        marginRight:"auto",
        borderRadius:"100px",
        top:"70%",
        zIndex:30
    }
   
}))



function Contact(props) {
    const classes = useStyles(props)

    return (
        <div className={classes.root}>
            <span className={classes.title}>44</span>
            <span className={classes.subtitle}>The page you're looking for is not avaliable</span>
            <Button className={classes.button}  variant="contained" onClick={()=>props.history.push("/home")}>Back to Home</Button>
            <AnimatedCircle style={{height:"100%"}} bottom="-45%" error404={true}/>
        </div>
    );
}

export default Contact;