import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import * as Constant from '../Constants'

const useStyles = makeStyles((theme) => ({
    backbutton:{
        position:"fixed", 
        display:"block",
        left:"8vw",
        top:props => !props.isMobile ? "5vh" : '90vh',
        zIndex:1000,
        color:Constant.TEXT_COLOR_1,
        // height:"5vh", height is in styles
    },
    backbuttonicon:{
        background:"rgba(0,0,0,0.7)",
        borderRadius:"50%",
        paddingTop:"10px",
        paddingBottom:"10px",
        paddingLeft:"13px",
        paddingRight:"2px",
        animation: `$movingIcon 0.5s infinite`,
        animationDuration: "3s",
    },
    "@keyframes movingIcon" :{
        "0%": { transform: "translate(0px, 0px)" },
        "12.5%": { transform: "translate(-5px, 0px)" },
        "25%": { transform: "translate(5px, 0px)" },
        "50%": { transform: "translate(0px, 0px)" },
        "100%": { transform: "translate(0px, 0px)" },
    },
}))

function ProjectsBackButton(props){
    const {backbutton, backbuttonicon} = useStyles(props);      
    const size = props.isMobile ? "3vh" : "5vh"
    const arrowStyle = {display:"block", height:size, width:size}

    return (
        <div onClick={() => props.history.push("/projects")} className={backbutton}>
            <ArrowBackIosIcon className={backbuttonicon} style={arrowStyle} />
        </div>
    )
}

export default ProjectsBackButton