import React from 'react';
import { withRouter, Switch, Route} from 'react-router-dom';
import { connect } from 'react-redux';
import Navbar from './Navbar/Navbar'
import Projects from './Projects';
import Contact from './Contact';
import Error404 from './Error404';
import Home from './Home';
import * as Constant from "../Constants"
import ProjectTemplate from './Projects/ProjectTemplate';

function DesktopLayout(props) {
    
    if (window.location.pathname.replace("/", "") === ""){
        props.history.push("/home")
    }

    return (
        <main>
            <Navbar {...props}>

                <Switch>
                    {
                        Constant.PROJECT_DATA.map((data) => (
                            <Route key={data.link} exact path={"/projects/" + data.link}>
                                <ProjectTemplate {...props}>
                                    {data.component}
                                </ProjectTemplate>
                            </Route>
                        ))
                    }
                    <Route key={"home"} exact path="/home">
                        <Home {...props} />
                    </Route>
                    <Route key={"projects"} exact path="/projects">
                        <Projects {...props} />
                    </Route>
                    <Route key={"contact"} exact path="/contact">
                        <Contact />
                    </Route>
                    <Route key={"error"} path="/*">
                        <Error404 {...props}/>
                    </Route>
                </Switch>
            </Navbar>   
        </main>
        
    );
}

const mapStateToProps = state => {
    return {
    }
  }

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DesktopLayout));