import React, { useState } from 'react'
import TiltPhaseSix from '../Components/TiltPhaseSix'
import { makeStyles } from '@material-ui/core/styles';
import * as Constant from '../Constants'
import { GridList} from '@material-ui/core';
import AnimatedText from '../Components/AnimatedText';
import AnimatedCircle from '../Components/AnimatedCircle';

const customCardStyles = makeStyles((theme) => ({
    img: {
        width: "24vw",
        height: "24vw",
        margin: 0,
        padding: 0,
        display:"block",
        // filter: props => `opacity(.8) drop-shadow(0 0 0 ${props.img_color})`,
    },
    cardContainer:{
        position: "relative",
        textAlign: "center",
        margin:"5px"
    },
    text: {
        background: props => props.img_color,
        color: props => props.text_color,
        fontWeight:"bolder",
        // opacity:0.9,
        position: "absolute",
        bottom:"0%",
        marginBottom:0,
        padding:"10px 0 10px 0",
        width:"24vw"
    },
    viewProject:{
        background: "black",
        color: "white",
        fontWeight:"bolder",
        opacity:0.9,
        position: "absolute",
        top:"0%",
        marginTop:0,
        padding:"10px 0 10px 0",
        width:"24vw",
        height:"24vw",
        lineHeight:"24vw"
    }
}))

function CustomCard(props) {
    const classes = customCardStyles(props);
    const [isViewProject, setIsViewProject] = useState(false);
    return (
        <TiltPhaseSix> 
            <div className={classes.cardContainer} onClick={()=>props.history.push("/projects/" + props.link)} onMouseOver={()=> setIsViewProject(true)}  onMouseLeave={()=> setIsViewProject(false)}>
                <img className={classes.img} src={props.image} alt={props.text}/>
                <p className={classes.text}>{props.text}</p>
                {
                    isViewProject?
                    <p className={classes.viewProject}>View Project</p>
                    :
                    ''
                }
            </div>
        </TiltPhaseSix>
  );
}

const useStyles = makeStyles((theme) => ({
    '@global': {
        '*::-webkit-scrollbar': {
          width: '0.4em'
        },
        '*::-webkit-scrollbar-track': {
        '-webkit-box-shadow': 'inset 0 0 6px rgba(0,0,0,0.00)'
        },
        '*::-webkit-scrollbar-thumb': {
        backgroundColor: 'rgba(0,0,0,.1)',
        outline: '1px solid slategrey'
        }
    },
    root: {
        height: "100vh",
        overflow:"hidden"
    },
    title:{
        color: Constant.TEXT_COLOR_1,
        // width:"100%",
        // background:"#000000",
        // opacity:0.5,
        // textAlign:"center",
        left:"10%",
        position: "absolute",
        margin:0,
        fontSize:'500%',
        fontWeight:"bolder",
        zIndex: 50,
    },
    gridList: {
        flexWrap: 'nowrap',
        position:"absolute",
        zIndex:50,
        width:"85vw",
        top:"30vh",
        left:"10vw",
        overflowY:"hidden"
    }

}))

function Projects(props) {
    const classes = useStyles(props);      
  
    return (
        <div className={classes.root}>
            <h1 className={classes.title}>
                <AnimatedText text="MY WORK" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>

            <GridList className={classes.gridList} cols={2.5}>
                {
                    Constant.PROJECT_DATA.map((d, index)=>(
                        <CustomCard {...d} {...props} key={index}></CustomCard>
                    ))
                }
            </GridList>

            <AnimatedCircle bottom="-30%" style={{height:"100%"}}></AnimatedCircle>
            <br/>
        </div>
    );
  }
  
export default Projects;