import React, {useState} from 'react';
// import PropTypes from 'prop-types';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import { makeStyles } from '@material-ui/core/styles';
import CustomListItem from './CustomListItem'
import * as Constant from "../../Constants"
import logo from '../../images/logo.png'

const drawerWidth = 100;

const useStyles = makeStyles((theme) => ({
  root: {
    // display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  list: {
    background: Constant.TEXT_COLOR_2,
    height:"100vh"
  },
  listItem: {
    paddingLeft:0,
    paddingRight:0,
    marginTop:20,
    marginBottom:20,
  },
  logo: {
    width:40,
    height:40,
    display: "block",
    marginLeft: "auto",
    marginRight: "auto",
    paddingTop:10
  }, 
  content:Constant.ROOT_STYLE
}));

function changeTabs(props, setSelectedTab, text){
  if (window.location.pathname.replace("/", "") !== text) {
    setSelectedTab(text)
    props.history.push("/" + text)
  }
}

function ResponsiveDrawer(props) {
  const classes = useStyles(props);
  const buttonList = Constant.BUTTON_LIST
  const socialList = Constant.SOCIAL_LIST
  const [hoverIconName, setHoverIconName] = useState(null);
  const [selectedTab, setSelectedTab] = useState(window.location.pathname.replace("/", ""));

  const drawer = (
    <div className={classes.list}>
      <img className={classes.logo} src={logo} alt="logo"/>
      <List>
        {Object.keys(buttonList).map((text, index) => (
            <div key={text} onClick={() => changeTabs(props, setSelectedTab, text)} onMouseOver={() => setHoverIconName(text)} onMouseLeave={() => setHoverIconName(null)}>
              <ListItem className={classes.listItem}>
                <CustomListItem text={text} isSelected={selectedTab === text} isHover={hoverIconName===text} icon={buttonList[text]} />
              </ListItem>
            </div>
        ))}
      </List>

      <List style={{bottom:0, position:"absolute"}}>
        {Object.keys(socialList).map((text, index) => (
            <div key={text} onClick={() => window.open(socialList[text]['link'],'_blank')} onMouseOver={() => setHoverIconName(text)} onMouseLeave={() => setHoverIconName(null)}>
              <ListItem className={classes.listItem}>
                <CustomListItem text={text} isSelected={false} isHover={hoverIconName===text} icon={socialList[text]['icon']}/>
              </ListItem>
            </div>
        ))}
      </List>
    </div>
  );       

  return (
    <div className={classes.root} >
      <nav className={classes.drawer} aria-label="">
          <Drawer
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
      </nav>
      <main className={classes.content}>
        {props.children}
      </main>
    </div>
  );
}

// ResponsiveDrawer.propTypes = {
//   /**
//    * Injected by the documentation to work in an iframe.
//    * You won't need it on your project.
//    */
//   window: PropTypes.func,
// };

export default ResponsiveDrawer;
