import Carousel from 'react-material-ui-carousel'
import { Paper } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';



const useStyles = makeStyles((theme) => ({
    carousel:{
        width: props=> props.width === undefined? "70vw" : props.width,
        height: props=> props.height,
        marginLeft: props=> props.marginLeft === undefined? "15vw" : props.marginLeft,
        zIndex:100
    }, 
    carouselImg:{
        width: props=> props.width === undefined? "70vw" : props.width,
        opacity:0.8,
        marginLeft:0
    }, 
}))

function CustomCarousel(props) {
    const classes = useStyles(props);   

    return (
        <Carousel className={classes.carousel} animation="slide" navButtonsAlwaysVisible={true}>
        {
            props.items.map( (item, i) => 
            <Paper style={{background:"#000000"}} key={i}>
                <img className={classes.carouselImg} src={item} alt="Website Showcase"/>
            </Paper>
            
            )
        }
        </Carousel>
    )
}

export default CustomCarousel