import React from 'react';
import Dropzone from 'react-dropzone'
import axios from 'axios';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Button, Tabs, Tab, Dialog, DialogContent, IconButton, Typography, AppBar, Toolbar, Backdrop } from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import PropTypes from 'prop-types';
import CloseIcon from '@material-ui/icons/Close';
import GetAppIcon from '@material-ui/icons/GetApp';
import Webcam from "react-webcam";
import CustomSnackbar from '../../Components/CustomSnackbar';
import RotateLeftIcon from '@material-ui/icons/RotateLeft';

function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box p={3}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };

const useStyles = makeStyles((theme) => ({
    root:{
        height: "90vh",
        width: "90vw",
        // marginLeft:"10vw",
        // marginTop:"10vh",
        background:"linear-gradient(170deg, #000000 0%, #04619f 74%)"
    },
    paragraph:{
        color:"#FFF",
    },
    dropzone: {
        height: "30vh",
        width: "100%",
        // overflow:"hidden",
    },
    dropzoneText: {
        display:"block",
        textAlign:"center",
        marginTop:"7vh",
        color:"#FFF",
        marginBottom:"10px",
    },
    dropzoneIcon:{
        display: "block",
        marginLeft: "auto",
        marginRight: "auto",
        color:"#FFF"
    },
    webcam: {
        maxWidth:"80vw",
        maxHeight:"55vh",
        display:"block",
        margin:"auto"
    }
}))

function handleUpload(files, setResImg, setIsLoading){
    if (files.length === 0){
        return
    }
    setIsLoading(true)
    let form_data = new FormData();
    let file = files[0]
    form_data.append('image', file);

    axios.post("https://cryptic-thicket-91896.herokuapp.com/detect", form_data, {
    // axios.post("http://localhost:8000/wasd", form_data, {
        headers: {
          'content-type': 'multipart/form-data'
        }
    })
    .then(res => {
        setResImg("data:image/png;base64, " + res.data.result)
        setIsLoading(false)
    })
    .catch(err => {
        console.log(err)
        setIsLoading(false)
    })
}

function tabProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
}

const videoConstraints = {
    width: 1280,
    height: 720,
    facingMode: "user"
  };
   
const WebcamCapture = (props) => {
    const webcamRef = React.useRef(null);

    function dataURLtoFile(dataurl, filename) {
        var arr = dataurl.split(','),
            mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), 
            n = bstr.length, 
            u8arr = new Uint8Array(n);  
        while(n--){
            u8arr[n] = bstr.charCodeAt(n);
        } 
        return new File([u8arr], filename, {type:mime});
    }

    const capture = React.useCallback(() => {
        const imageSrc = webcamRef.current.getScreenshot();
        try{
            const files = [dataURLtoFile(imageSrc, "captureImg.jpg")]
            handleUpload(files, props.setResImg, props.setIsLoading)
        } catch{
            props.setSnackbarOpen(true)
        }
        },[webcamRef]
    );
    const {webcam} = useStyles(props);      
    const [snackbaropen, setSnackbarOpen] = React.useState(true)

    return (
        <>
        <Webcam
            className={webcam}
            audio={false}
            ref={webcamRef}
            screenshotFormat="image/jpeg"
            videoConstraints={videoConstraints}
        />

        <CustomSnackbar setSnackbarOpen={setSnackbarOpen} text={"Note: landmarks may not be detected if your face is too close to the camera"} open={snackbaropen} severity="info"/>


        <Button onClick={capture} style={{width:"100%", position:"absolute", bottom:0, left:0}} variant="contained" color="primary">Take a Picture</Button>
        </>
    );
};

function CustomDropzone(props){
    const {dropzone, dropzoneText, dropzoneIcon} = useStyles(props);      
    const [isHover, setIsHover] = React.useState(false)
    const divStyle = isHover? {borderStyle:"solid", borderColor:"#FFF", background:"#9c9c9c", color:"#FFF"} : {borderStyle:"solid", borderColor:"#FFF"} 

    return (
        <Dropzone 
            onDrop={files => handleUpload(files, props.setResImg, props.setIsLoading)}
            onDragOver={()=>setIsHover(true)} 
            onDragLeave={()=>setIsHover(false)}
            >
            {({getRootProps, getInputProps}) => (
                <div {...getRootProps()} className={dropzone} style={divStyle} >
                    <input {...getInputProps()} />
                        <Typography className={dropzoneText} component={'span'} variant={'body1'}>
                            <span style={{fontWeight:"bold"}}>Choose an image file</span> or drag it here 
                        </Typography>
                    <GetAppIcon className={dropzoneIcon}/>
                </div>
            )}
        </Dropzone>
    )
}

function IntroPage(props){
    const {paragraph} = useStyles(props);      
    return(
        <>
            <Typography component={'p'} variant={'body1'} className={paragraph}>
                This Face Detector API uses You Only Look Once tiny (YOLOv4-tiny), a small version of 
                YOLOv4 designed for mobile devices. Such networks designed are less accurate but 
                speed-optimised to be able to run in real-time. 
            </Typography>
            <br/>
            <Typography component={'p'} variant={'body1'} className={paragraph}>
                Click on "Upload Image" or "Take a Picture" to test out my Face Detector API.
            </Typography>
        </>
    )
}

function FaceLmkCustomDialog(props){
    const [resImg, setResImg] = React.useState(null)
    const [isLoading, setIsLoading] = React.useState(false)
    const [tabValue, setTabValue] = React.useState(0)
    const [snackbaropen, setSnackbarOpen] = React.useState(false)
    const [wakeup, setWakeup] = React.useState(false)
    const classes = useStyles(props);    
    
    if(!wakeup && (tabValue===1 || tabValue===2)){
        axios.post("https://cryptic-thicket-91896.herokuapp.com/open_sesame", {}, {})
        setWakeup(true)
    }

    function handleClose(){
        setWakeup(false)
        props.setDialogOpen(false)
    }

    const handleTabChange = (event, newValue) => {
        setTabValue(newValue);
    };

    return (
        <Dialog PaperProps={{className:classes.root}} fullScreen onClose={handleClose} aria-labelledby="simple-dialog-title" open={props.open}>
                <CustomSnackbar setSnackbarOpen={setSnackbarOpen} text={"Unable to access camera! User must allow access to camera in browser settings"} open={snackbaropen} severity="error"/>
                <AppBar elevation={0} style={{position: 'relative', zIndex: 30, color: '#FFF', background:"rgba(0,0,0,0)"}}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6">
                            Face Detection API
                        </Typography>
                    </Toolbar>
                    {
                        resImg === null?
                        <Tabs value={tabValue} onChange={handleTabChange} aria-label="simple tabs example">
                            <Tab label="Introduction" {...tabProps(0)} />
                            <Tab label="Upload Image" {...tabProps(1)} />
                            <Tab label="Take a Picture" {...tabProps(2)} />
                        </Tabs>
                        :
                        ''
                    }
                </AppBar>
                <DialogContent>
                    <Backdrop style={{zIndex: 20, color: '#fff'}} open={isLoading}>
                        <Typography component={'span'} variant={'body1'}>
                            Uploading image... Please wait...  
                        </Typography>
                        <CircularProgress color="inherit" style={{marginLeft:"10px"}} />
                    </Backdrop>

                    {
                        resImg === null?
                        <>
                            <TabPanel value={tabValue} index={0}>
                                <IntroPage/>
                            </TabPanel>
                            <TabPanel value={tabValue} index={1}>
                                <CustomDropzone setResImg={setResImg} setIsLoading={setIsLoading}/>
                            </TabPanel>
                            <TabPanel value={tabValue} index={2}>
                                <WebcamCapture setResImg={setResImg} setIsLoading={setIsLoading} setSnackbarOpen={setSnackbarOpen}/>
                            </TabPanel>
                        </>
                        :
                        <>
                            <img src={resImg} style={{maxWidth: "80vw", maxHeight:"70vh", minWidth:"55vw", display:"block", marginLeft:"auto", marginRight:"auto"}} alt={"Uploaded picture with face landmarks"}/>
                            <Button onClick={()=>setResImg(null)} style={{left:0, position:"absolute", bottom:0, width:"100%"}}  color="primary" variant="contained">
                                <RotateLeftIcon style={{marginRight:"5px"}}/>
                                Reset 
                            </Button>
                        </>
                    }
                </DialogContent>
        </Dialog>
    )
}

export default FaceLmkCustomDialog