import React from 'react';
import AnimatedCircle from '../../Components/AnimatedCircle';
import ProjectsBackButton from '../../Components/ProjectsBackButton';

function ProjectTemplate(props) {
    // const [snackbaropen, setSnackbarOpen] = React.useState(false);

    const childrenWithProps = React.Children.map(props.children, child => {
        if (React.isValidElement(child)) {
          return React.cloneElement(child, { /*insert props here*/ });
        }
        return child;
      });

    return (
        <AnimatedCircle bottom="-10%" translateYHeight="-360vh">
            {/* <CustomSnackbar setSnackbarOpen={setSnackbarOpen} text="Feature coming soon!" open={snackbaropen} severity="error" /> */}
            <ProjectsBackButton {...props}/>

            {childrenWithProps}
        </AnimatedCircle>
    );
}

export default ProjectTemplate;