import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import * as Constant from '../../Constants'
import AnimatedText from '../../Components/AnimatedText';
import CustomCarousel from '../../Components/CustomCarousel';
import space_img_1 from '../../images/space_1.png'
import space_img_2 from '../../images/space_2.png'
import space_header_1 from '../../images/space_header_1.png'
import space_header_2 from '../../images/space_header_2.png'
import space_header_3 from '../../images/space_header_3.png'
import space_header_4 from '../../images/space_header_4.png'

const useStyles = makeStyles((theme) => ({
    root: {
        // height: "100vh",
        // overflow:"hidden",
    },
    img :{
        maxWidth:"70vw",
        maxHeight:"80vh",
        display:"block",
        marginLeft:"auto",
        marginRight:"auto",
        position:"relative",
        zIndex:30
    },
    button:{
        position:"relative",
        zIndex:30,
        marginTop: "15vh",
        width:"12vw",
        marginLeft:"44vw",
    },
    link:{
        fontWeight:"bolder",
        color:"#F8F8FF"
    },
    subtitle: {
        fontSize: "300%",
        marginLeft:"15vw",
        color: Constant.TEXT_COLOR_1,
        fontWeight:"bolder"
    },
    title: {
        textAlign: "center",
        color: Constant.TEXT_COLOR_1,
        width:"70vw",
        marginLeft:"15vw",
        fontSize: "400%",
        margin: 0,
        display:"block",
        position:"relative",
        zIndex:10
    },
    carousel:{
        width:"70vw",
        marginLeft:"15vw",
        zIndex:100
    }, 
    carouselImg:{
        width:"70vw",
        opacity:0.8,
        marginLeft:0
    }, 
    paragraph: Constant.PROJECT_PARAGRAPH
}))

const Space = React.memo(function Space(props) {
    const classes = useStyles(props);      

    return (
        <>
            <h1 className={classes.title} style={{marginTop:"10vh"}}>
                <AnimatedText text="Schedule Planner and" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>

            <h1 className={classes.title}>
                <AnimatedText text="Course Evaluation System" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h1>

            <h2 className={classes.subtitle} >
                <AnimatedText text="Introduction" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h2>

            <div align="justify">
                <p className={classes.paragraph}>
                Schedule Planner and Course Evaluation system, or SPACE, was created in 2019 by 
                Me. The goal of SPACE is to simplify timetable planning by providing 
                information about the modules in NTU. Additionally, SPACE empowers NTU students 
                through community-driven professors and module reviews. 
                </p>
            </div>

            <div align="justify">
                <p className={classes.paragraph}>
                This project is featured in a NTU School of Computer Science 
                and Engineering Marketing <a target="_blank" rel="noreferrer" className={classes.link} href="https://www.youtube.com/watch?v=6-jZxCkj0FM">
                     Youtube video 
                </a>
                </p>
            </div>

            <CustomCarousel height="16.84vw" items={[space_header_1,space_header_2,space_header_3,space_header_4]}/>


            <h2 className={classes.subtitle} >
                <AnimatedText text="NLP techniques" alt_color={Constant.TEXT_COLOR_2} ></AnimatedText>
            </h2>

            <div align="justify">
                <p className={classes.paragraph}>
                The abundance of reviews collected by SPACE over time can be overwhelming for students, making timetable planning difficult. Therefore, SPACE uses two NLP techniques to help students gain an overview of the reviews: "Sentimental Polarity Analysis" and "Adjective-Noun Pair Summarizer".
                </p>
            </div>


            <div align="justify">
                <p className={classes.paragraph}>
                SPACE uses sentimental polarity analysis to classify reviews into positive, neutral, or negative. Students can sort reviews by its sentimental rating.
                </p>
            </div>

            <img className={classes.img} src={space_img_1} alt="Classification of reviews with sentimental polarity analysis"></img>

            <div align="justify" style={{marginTop:"10vh"}}>
                <p className={classes.paragraph}>
                SPACE also use adjective-noun pair summarizer to summarise all reviews of each module into 10 Adjective-noun pair (e.g. "great course"). Students will be able to view the summarised description of each course.
                </p>
            </div>

            <img className={classes.img} src={space_img_2} style={{marginBottom: "10vh"}} alt="Summarizing a review into 2 adjective noun pair with adjective-noun pair summarizer"></img>

        </>
    );
})

export default Space;